<template>
    <div class="row">
        <div class="col-2">
            <label for="sample" class="btn btn-outline-secondary">Add Sample</label>
            <input type="file" accept="audio/*" id="sample" @change="addSampleOption">
        </div>
        <div class="col ms-2">
            <CustomSelect
            :options="allSamples"
            :default="mySample.name"
            @input="selectSample($event)"
            class="select"
            />
        </div>
    </div>
</template>

<script>
import CustomSelect from "@/components/CustomSelect.vue";

    export default {
        components:{
            CustomSelect,
        },
        props:['name','id','src', 'selected'],
        computed: {
            allSamples(){
                var samples = this.$store.state.samples;
                var allSamplesName=[];
                samples.forEach(sample => {
                    allSamplesName.push(sample.name);
                });
                return allSamplesName;
            },
            selectedSampleName(){
                var selectedSample = this.$store.getters.getSelectedSample;
                return selectedSample.name;
            },
            getAudioCtx(){
                return this.$store.getters.getAudioCtx;
            },
            mySample(){
                const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
                return chosenSample;
            },
        },
        methods: {
            readUploadedFile(file) {
              const temporaryFileReader=new FileReader();

              return new Promise((resolve, reject) => {
                temporaryFileReader.onerror=() => {
                  temporaryFileReader.abort();
                  reject(new DOMException("Problem parsing input file."));
                };

                temporaryFileReader.onload=() => {
                  resolve(temporaryFileReader.result);
                };
                temporaryFileReader.readAsArrayBuffer(file);
              });
            },
            async createAudioBuffer(arrayBuffer){
                var audioCtx = this.getAudioCtx;
                var buffer = await audioCtx.decodeAudioData(arrayBuffer);
                return buffer;
            },
            async addSampleOption(){
                var sampleFile = document.getElementById("sample").files[0];
                var arrayBuffer = await this.readUploadedFile(sampleFile);
                var buffer = await this.createAudioBuffer(arrayBuffer);
                this.$store.commit('addSamples',{sampleName:sampleFile.name,buffer:buffer});
            },
            selectSample(event){
                this.$store.commit('selectSample',event);
                this.$store.commit('initAudio', this.mySample.id);
            },
        }
    }
</script>

<style scoped>
    input{
        display: none;
    }
    option:checked{
        background-color:var(--bs-primary);
        color: ghostwhite;
    }

</style>