<template>
  <div class="home">
    <DrumMachine />
    <div class="timeline">
      <div style="display: grid;width: 100vw; grid-template-columns: 11rem auto auto; align-items: start;">
         <div style="height: 24px; width: 10rem; border-radius: 20px; margin: 1rem 0 1rem 1rem; border: 2px solid #006B5A">
            <p style="margin: 0 0 0 5px; color: ghostwhite">Channel</p>
        </div>
        <div class="divProgress">
          <div class="progress" id="progress">
            <p v-for="i in 4" :key="i" class="numberOfMesure" :style="{marginLeft : 17.5*(i-1)+0.25 + 'vw'}">{{i}}</p>
          </div>
        </div> 
      </div>
      <div class="samples">
        <Sample v-for="sample in samplesChosen" v-bind:key="sample.id" :id="sample.id" :name="sample.name" :src="sample.src" :selected="sample.selected" />
      </div>
      <button type="button" class="btn ms-3" style="background-color: #888888" @click="addSample">ADD SAMPLE</button>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import DrumMachine from '@/components/DrumMachine.vue'
import Sample from '@/components/Sample.vue'

export default {
  name: 'Sequencer',
  components: {
    DrumMachine, Sample
  },
  computed: {
    samplesChosen(){
      return this.$store.state.samplesChosen;
    },
    getAudioCtx(){
        return this.$store.getters.getAudioCtx;
    },
  },
  methods:{
    async addSample(){
      var sourceURL = require('../assets/kick.wav');
      var blob = await this.fetchFile(sourceURL);
      var arrayBuffer = await this.readUploadedFile(blob);
      var audioBuffer = await this.createAudioBuffer(arrayBuffer);
      this.$store.commit('addSamplesChosen',{sampleName:"Default", src:audioBuffer, selected:true});
    },
    playTick() {
      this.counter += 1;
      this.futureTickTime += this.counterTimeValue;
      if (this.counter > 15) {
        this.counter = 0;
      }
    },
    fetchFile(url){
      return new Promise((resolve, reject) => {
        fetch(url).then((response) => {
          if (response.ok) {
            resolve(response.blob());
          }
          reject(new Error('Network response was not ok.'));
        });
      }
    )},
    readUploadedFile(file) {
      const temporaryFileReader=new FileReader();

      return new Promise((resolve, reject) => {
        temporaryFileReader.onerror=() => {
          temporaryFileReader.abort();
          reject(new DOMException("Problem parsing input file."));
        };

        temporaryFileReader.onload=() => {
          resolve(temporaryFileReader.result);
        };
        temporaryFileReader.readAsArrayBuffer(file);
      });
    },
    async createAudioBuffer(arrayBuffer){
        var audioCtx = this.getAudioCtx;
        var buffer = await audioCtx.decodeAudioData(arrayBuffer);
        return buffer;
    },
  },
  async mounted(){
    this.$store.commit('createAudioCtx');
    var sourceURL = require('@/assets/kick.wav');
    var blob = await this.fetchFile(sourceURL);
    var arrayBuffer = await this.readUploadedFile(blob);
    var audioBuffer = await this.createAudioBuffer(arrayBuffer);
    this.$store.commit('addDefaultSample',audioBuffer);
  }
}
</script>

