<template>
    <fieldset class="reset-this redo-fieldset">
        <legend class="reset-this redo-legend">SAMPLE CONTROL</legend>
        <div class="row">
            <div class="col-3">
                <SampleChooser :name="mySample.name" :id="mySample.id" :src="mySample.src" :selected="mySample.selected"/>
            </div>
            <div class="col-6">
                <SampleFxTab :name="mySample.name" :id="mySample.id" :src="mySample.src" :selected="mySample.selected" />
            </div>
            
        </div>
    </fieldset>
</template>

<script>
import SampleChooser from '@/components/SampleChooser.vue'
import SampleFxTab from '@/components/SampleFxTab.vue'


export default {
    components: {
        SampleChooser,SampleFxTab
    },
    props:['name','id','src', 'selected'],
    computed:{
        mySample(){
            const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
            return chosenSample;
        },
    },
}

</script>

