    border: 2px solid rgb(96, 100, 105);
    border-radius: 0.5rem;
    display: flex;
    position: relative;
    box-sizing: border-box;
    background: rgba(0, 0, 0, 0) linear-gradient(190deg, rgb(25, 25, 29) 0%, rgb(48, 48, 54) 50%, rgb(10, 14, 10) 51%, rgb(41, 41, 45) 100%) repeat scroll 0% 0%;
    transition: border-color 0.2s ease 0s;
    width: 92px;
    height: 52px;
}

.labelInputBpm {
    color: rgb(144, 149, 153);
    background-color: rgb(32, 36, 39);
    font-weight: 600;
    font-size: 0.6rem;
    margin: 0px;
    padding: 0px 4px;
    position: absolute;
    left: 0.5rem;
    top: -0.6em;
    letter-spacing: 0.1em;
    display: block;
    line-height: 1em;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
}

.bpm-text-input {
    -webkit-appearance: none;
    appearance: textfield;
}

.inputBpm {
    color: rgb(244, 83, 58);
    background-color: transparent;
    font-weight: 500;
    font-size: 1.5rem;
    margin: 0px;
    padding: 0.3rem 0px 0px 0.7rem;
    position: relative;
    z-index: 1;
    width: 3.5rem;
    display: block;
    border: medium none;
    outline: none;
}

.divInputButtons {
    flex-direction: column;
    display: flex;
    box-sizing: border-box;
}

.inputButton {
    color: white;
    background-color: transparent;
    padding: 0px;
    border: medium none;
    font-weight: bold;
    width: 2rem;
    flex: 1 1 auto;
    touch-action: manipulation;
}

#inputButtonUp{
    border-radius: 0px 0.5rem 0px 0px;
}

#inputButtonDown{
    border-radius: 0px 0px 0.5rem;
}

.divInputButtons button:active{
    background-color: black ;
}

</style>
