<template>
    <div class="row">
        <div class="col-3">
            <input type="range" min="0" max="100" step="1" value="100" :id="'volume'+mySample.id" @input="updateGain">
            <p>Gain : {{mySample.fxParams.gainValue}}</p>
        </div>
        <div class="col-3">
            <input type="range" min="-100" max="100" step="1" value="0" list="magnet" :id="'pan'+mySample.id" @input="updatePan">
            <datalist id="magnet">
                <option value="0"></option>
            </datalist>
        </div>
    </div>
</template>

<script>

export default {
    props:["id", "name", "src", "selected"],
    computed:{
        mySample(){
            const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
            return chosenSample;
        },
        getAudioCtx(){
            return this.$store.getters.getAudioCtx;
        },
    },
    methods:{
        updateGain(){
            var id = 'volume'+this.mySample.id;
            var value = (document.getElementById(id).value/100);
            this.$store.commit('updateGain',{value: value , sampleId: this.mySample.id});
        },
        updatePan(){
            var id = 'pan'+this.mySample.id;
            var value = (document.getElementById(id).value/100);
            this.$store.commit('updatePan',{value: value , sampleId: this.mySample.id});
            this.mySample.audioNode.pan.pan.setValueAtTime(value, this.getAudioCtx.currentTime);
        }
    }
}
</script>

