<template>
    <h1 style="color: white">OUI</h1>
    <SampleControl :name="mySample.name" :id="mySample.id" :src="mySample.src" :selected="mySample.selected" :class="{hidden: mySample.selected==false}" class="sampleControl" />
    <div class="centerDiv" style="color: white; height: 4rem; margin: 1rem 1rem">
        <div :class="{selectedSample: mySample.selected==true}" :id='mySample.id' style="display: inline-block; height: 4rem; vertical-align: top; background: #888888; width: 10rem; border-radius: 5px; margin-right: 0.5rem" @click="selectChannel">
            <p style="margin: 1.25rem 15px 1.25rem 10px; display: inline-block">{{mySample.name.substring(0,7)}}</p>
            <audio ref="audioPlayer">
                <source ref="audioSource" :src="mySample.src">
            </audio>
            <button v-if="mySample.mute===false" type="button" class="btn" style="z-index:2; display: inline-block;  float:right; margin: 0.75rem auto" @click="mute">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-volume-up-fill" viewBox="0 0 16 16">
                    <path d="M11.536 14.01A8.473 8.473 0 0 0 14.026 8a8.473 8.473 0 0 0-2.49-6.01l-.708.707A7.476 7.476 0 0 1 13.025 8c0 2.071-.84 3.946-2.197 5.303l.708.707z"/>
                    <path d="M10.121 12.596A6.48 6.48 0 0 0 12.025 8a6.48 6.48 0 0 0-1.904-4.596l-.707.707A5.483 5.483 0 0 1 11.025 8a5.483 5.483 0 0 1-1.61 3.89l.706.706z"/>
                    <path d="M8.707 11.182A4.486 4.486 0 0 0 10.025 8a4.486 4.486 0 0 0-1.318-3.182L8 5.525A3.489 3.489 0 0 1 9.025 8 3.49 3.49 0 0 1 8 10.475l.707.707zM6.717 3.55A.5.5 0 0 1 7 4v8a.5.5 0 0 1-.812.39L3.825 10.5H1.5A.5.5 0 0 1 1 10V6a.5.5 0 0 1 .5-.5h2.325l2.363-1.89a.5.5 0 0 1 .529-.06z"/>
                </svg>
            </button>
            <button v-if="mySample.mute===true" type="button" class="btn" style="z-index:2, display: inline-block; float:right; margin: 0.75rem auto" @click="mute">
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-volume-mute-fill" viewBox="0 0 16 16">
                    <path d="M6.717 3.55A.5.5 0 0 1 7 4v8a.5.5 0 0 1-.812.39L3.825 10.5H1.5A.5.5 0 0 1 1 10V6a.5.5 0 0 1 .5-.5h2.325l2.363-1.89a.5.5 0 0 1 .529-.06zm7.137 2.096a.5.5 0 0 1 0 .708L12.207 8l1.647 1.646a.5.5 0 0 1-.708.708L11.5 8.707l-1.646 1.647a.5.5 0 0 1-.708-.708L10.793 8 9.146 6.354a.5.5 0 1 1 .708-.708L11.5 7.293l1.646-1.647a.5.5 0 0 1 .708 0z"/>
                </svg>
            </button>
        </div>
        <div v-for="i in nbMesures" :key="i" style="display: inline-block; height: 4rem; margin-right: 0.5vw" :class="{start_mesure: i==1, border_right_mesures : i != 4}"> 
            <div v-for="n in nbTemps" :key="n+nbTemps*(i-1)" :ref="'temps-ref-'+(n+nbTemps*(i-1))" :name="mySample.name" style="background-color: #888888; height: 4rem; display: inline-block; border-radius: 5px;" :style="{width: (15/nbTemps + (4-nbTemps)/(2*nbTemps) + 'vw'), 'margin-right': 0.5 +'vw'}"   @click="active(n+nbTemps*(i-1))">
            </div>
        </div>
        <div class="d-lg-inline-block d-none" style="display: inline-block; vertical-align: top; margin-left: 1em; height: 4rem"> 
            <label syle="display: block;" for="temps">Nombre de temps :</label>
            <input style="display: block; width: 8rem; " type="number" id="temps" name="temps"
                min="1" max="8" v-model.number="temps">
        </div>
        <button v-if="$store.state.samplesChosen.length!==1" type="button" class="btn btn-danger" style="vertical-align: top;margin : 1em auto 1em 2em;" @click="deleteSample">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
            </svg>
        </button>
    </div>
</template>

<script>
import SampleControl from '@/components/SampleControl.vue'

export default ({
    data(){
        return {
            nbTemps: 4,
            nbMesures: 4,
        }
    },
    components:{
        SampleControl,
    },
    methods:{
        active(id){
            var sample = ("temps-ref-" + id);
            var div = this.$refs[sample];
            this.$store.commit('setNote', {sampleId: this.mySample.id, noteId:id-1});
            if(div.classList.contains("active")){
                div.classList.remove("active");
            }
            else{
                div.classList.add("active");
                this.$store.commit('initAudio',this.mySample.id);
                if(!this.$store.state.isPlaying){
                    this.$store.commit('playNote', this.mySample.id);
                }
            }
        },
        deleteSample(){
            this.$store.commit('deleteSamplesChosen',this.id);
        },
        selectChannel(){
            this.$store.commit('selectChannel', this.mySample.id);
        },
        mute(event){
            event.stopPropagation();
            if(event.shiftKey){
                console.log("SHIFT");
                this.$store.commit("activeOnlyThisSong", this.id);
            }
            else{
                this.$store.commit("mute", this.id);
            }
        },
    },
    props: ['id', 'name', 'src', 'selected'],
    computed: {
        getAudioCtx(){
            return this.$store.getters.getAudioCtx;
        },
        mySample(){
            const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
            return chosenSample;
        },
        temps:{
            get(){
                return this.nbTemps;
            },
            set(newNbTemps){
                if (newNbTemps===5){
                    if (this.nbTemps===6){
                        newNbTemps = 4 ;
                    }
                    else{
                        newNbTemps = 6;
                    }    
                }
                else if (newNbTemps===7){
                    if (this.nbTemps===8){
                        newNbTemps = 6 ;
                    }
                    else{
                        newNbTemps = 8;
                    }   
                }
                else if(newNbTemps===""){
                    return;
                }
                var divsActivate = document.getElementsByName(this.name);
                for(var i=0; i<divsActivate.length; i++){
                    divsActivate[i].classList.remove("active");
                }
                this.nbTemps = newNbTemps;
            }
        }
    },
})
</script>

<style scoped>
.btn:active, .btn:focus{
    box-shadow: none;
}

.sampleControl{
    position: absolute;
    top: 11rem;
}
.centerDiv{
    margin: 1rem;
}
.active{
    background-color: #F0A500 !important;
}

.border_right_mesure{
    border-right: 0.2rem solid black; height: 3rem;
}

.border_bottom{
    border-bottom: 0.1rem solid white
}

.margin_left{
    margin-left: 1rem;
}

.start_mesure{
    margin-left: 0.5rem;
}

.selectedSample{
    background-color: #666 !important;
}

.hidden{
    display: none;
}
</style>>

